ign: middle;\n  cursor: pointer;\n\n  + .form-check-inline {\n    margin-left: .75rem;\n  }\n\n  &.disabled {\n    color: $text-muted;\n    cursor: $cursor-disabled;\n  }\n}\n\n\n// Form control feedback states\n//\n// Apply contextual and semantic states to individual form controls.\n\n.form-control-feedback {\n  margin-top: ($spacer * .25);\n}\n\n.form-control-success,\n.form-control-warning,\n.form-control-danger {\n  padding-right: ($input-padding-x * 3);\n  background-repeat: no-repeat;\n  background-position: center right ($input-height / 4);\n  background-size: ($input-height / 2) ($input-height / 2);\n}\n\n// Form validation states\n.has-success {\n  @include form-control-validation($brand-success);\n\n  .form-control-success {\n    background-image: $form-icon-success;\n  }\n}\n\n.has-warning {\n  @include form-control-validation($brand-warning);\n\n  .form-control-warning {\n    background-image: $form-icon-warning;\n  }\n}\n\n.has-danger {\n  @include form-control-validation($brand-danger);\n\n  .form-control-danger {\n    background-image: $form-icon-danger;\n  }\n}\n\n\n// Inline forms\n//\n// Make forms appear inline(-block) by adding the `.form-inline` class. Inline\n// forms begin stacked on extra small (mobile) devices and then go inline when\n// viewports reach <768px.\n//\n// Requires wrapping inputs and labels with `.form-group` for proper display of\n// default HTML form controls and our custom form controls (e.g., input groups).\n\n.form-inline {\n\n  // Kick in the inline\n  @include media-breakpoint-up(sm) {\n    // Inline-block all the things for \"inline\"\n    .form-group {\n      display: inline-block;\n      margin-bottom: 0;\n      vertical-align: middle;\n    }\n\n    // Allow folks to *not* use `.form-group`\n    .form-control {\n      display: inline-block;\n      width: auto; // Prevent labels from stacking above inputs in `.form-group`\n      vertical-align: middle;\n    }\n\n    // Make static controls behave like regular ones\n    .form-control-static {\n      display: inline-block;\n    }\n\n    .input-group {\n      display: inline-table;\n      width: auto;\n      vertical-align: middle;\n\n      .input-group-addon,\n      .input-group-btn,\n      .form-control {\n        width: auto;\n      }\n    }\n\n    // Input groups need that 100% width though\n    .input-group > .form-control {\n      width: 100%;\n    }\n\n    .form-control-label {\n      margin-bottom: 0;\n      vertical-align: middle;\n    }\n\n    // Remove default margin on radios/checkboxes that were used for stacking, and\n    // then undo the floating of radios and checkboxes to match.\n    .form-check {\n      display: inline-block;\n      margin-top: 0;\n      margin-bottom: 0;\n      vertical-align: middle;\n    }\n    .form-check-label {\n      padding-left: 0;\n    }\n    .form-check-input {\n      position: relative;\n      margin-left: 0;\n    }\n\n    // Re-override the feedback icon.\n    .has-feedback .form-control-feedback {\n      top: 0;\n    }\n  }\n}\n","// Form validation states\n//\n// Used in _forms.scss to generate the form validation CSS for warnings, errors,\n// and successes.\n\n@mixin form-control-validation($color) {\n  // Color the label and help text\n  .form-control-feedback,\n  .form-control-label,\n  .form-check-label,\n  .form-check-inline,\n  .custom-control {\n    color: $color;\n  }\n\n  // Set the border and box shadow on specific inputs to match\n  .form-control {\n    border-color: $color;\n\n    @if $enable-rounded {\n      &:focus {\n        box-shadow: $input-box-shadow, 0 0 6px lighten($color, 20%);\n      }\n    }\n  }\n\n  // Set validation states also for addons\n  .input-group-addon {\n    color: $color;\n    border-color: $color;\n    background-color: lighten($color, 40%);\n  }\n}\n\n// Form control focus state\n//\n// Generate a customized focus state and for any input with the specified color,\n// which defaults to the `@input-border-focus` variable.\n//\n// We highly encourage you to not customize the default value, but instead use\n// this to tweak colors on an as-needed basis. This aesthetic change is based on\n// WebKit's default styles, but applicable to a wider range of browsers. Its\n// usability and accessibility should be taken into account with any change.\n//\n// Example usage: change the default blue border and shadow to white for better\n// contrast against a dark gray background.\n@mixin form-control-focus() {\n  &:focus {\n    color: $input-color-focus;\n    background-color: $input-bg-focus;\n    border-color: $input-border-focus;\n    outline: none;\n    @include box-shadow($input-box-shadow-focus);\n  }\n}\n\n// Form control sizing\n//\n// Relative text size, padding, and border-radii changes for form controls. For\n// horizontal sizing, wrap controls in the predefined grid classes. `<select>`\n// element gets special love because it's special, and that's a fact!\n\n@mixin input-size($parent, $input-height, $padding-y, $padding-x, $font-size, $line-height, $border-radius) {\n  #{$parent} {\n    height: $input-height;\n    padding: $padding-y $padding-x;\n    font-size: $font-size;\n    line-height: $line-height;\n    @include border-radius($border-radius);\n  }\n\n  select#{$parent} {\n    height: $input-height;\n    line-height: $input-height;\n  }\n\n  textarea#{$parent},\n  select[multiple]#{$parent} {\n    height: auto;\n  }\n}\n","// scss-lint:disable QualifyingElement\n\n//\n// Base styles\n//\n\n.btn {\n  display: inline-block;\n  font-weight: $btn-font-weight;\n  line-height: $btn-line-height;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: middle;\n  cursor: pointer;\n  user-select: none;\n  border: $input-btn-border-width solid transparent;\n  @include button-size($btn-padding-y, $btn-padding-x, $font-size-base, $btn-border-radius);\n  @include transition(all .2s ease-in-out);\n\n  &,\n  &:active,\n  &.active {\n    &:focus,\n    &.focus {\n      @include tab-focus();\n    }\n  }\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n  &.focus {\n    text-decoration: none;\n  }\n\n  &:active,\n  &.active {\n    background-image: none;\n    outline: 0;\n    @include box-shadow($btn-active-box-shadow);\n  }\n\n  &.disabled,\n  &:disabled {\n    cursor: $cursor-disabled;\n    opacity: .65;\n    @include box-shadow(none);\n  }\n}\n\n// Future-proof disabling of clicks on `<a>` elements\na.btn.disabled,\nfieldset[disabled] a.btn {\n  pointer-events: none;\n}\n\n\n//\n// Alternate buttons\n//\n\n.btn-primary {\n  @include button-variant($btn-primary-color, $btn-primary-bg, $btn-primary-border);\n}\n.btn-secondary {\n  @include button-variant($btn-secondary-color, $btn-secondary-bg, $btn-secondary-border);\n}\n.btn-info {\n  @include button-variant($btn-info-color, $btn-info-bg, $btn-info-border);\n}\n.btn-success {\n  @include button-variant($btn-success-color, $btn-success-bg, $btn-success-border);\n}\n.btn-warning {\n  @include button-variant($btn-warning-color, $btn-warning-bg, $btn-warning-border);\n}\n.btn-danger {\n  @include button-variant($btn-danger-color, $btn-danger-bg, $btn-danger-border);\n}\n\n// Remove all backgrounds\n.btn-outline-primary {\n  @include button-outline-variant($btn-primary-bg);\n}\n.btn-outline-secondary {\n  @include button-outline-variant($btn-secondary-border);\n}\n.btn-outline-info {\n  @include button-outline-variant($btn-info-bg);\n}\n.btn-outline-success {\n  @include button-outline-variant($btn-success-bg);\n}\n.btn-outline-warning {\n  @include button-outline-variant($btn-warning-bg);\n}\n.btn-outline-danger {\n  @include button-outline-variant($btn-danger-bg);\n}\n\n\n//\n// Link buttons\n//\n\n// Make a button look and behave like a link\n.btn-link {\n  font-weight: normal;\n  color: $link-color;\n  border-radius: 0;\n\n  &,\n  &:active,\n  &.active,\n  &:disabled {\n    background-color: transparent;\n    @include box-shadow(none);\n  }\n  &,\n  &:focus,\n  &:active {\n    border-color: transparent;\n  }\n  @include hover {\n    border-color: transparent;\n  }\n  @include hover-focus {\n    color: $link-hover-color;\n    text-decoration: $link-hover-decoration;\n    background-color: transparent;\n  }\n  &:disabled {\n    @include hover-focus {\n      color: $btn-link-disabled-color;\n      text-decoration: none;\n    }\n  }\n}\n\n\n//\n// Button Sizes\n//\n\n.btn-lg {\n  // line-height: ensure even-numbered height of button next to large input\n  @include button-size($btn-padding-y-lg, $btn-padding-x-lg, $font-size-lg, $btn-border-radius-lg);\n}\n.btn-sm {\n  // line-height: ensure proper height of button next to small input\n  @include button-size($btn-padding-y-sm, $btn-padding-x-sm, $font-size-sm, $btn-border-radius-sm);\n}\n\n\n//\n// Block button\n//\n\n.btn-block {\n  display: block;\n  width: 100%;\n}\n\n// Vertically space out multiple block buttons\n.btn-block + .btn-block {\n  margin-top: $btn-block-spacing-y;\n}\n\n// Specificity overrides\ninput[type=\"submit\"],\ninput[type=\"reset\"],\ninput[type=\"button\"] {\n  &.btn-block {\n    width: 100%;\n  }\n}\n","// Button variants\n//\n// Easily pump out default styles, as well as :hover, :focus, :active,\n// and disabled options for all buttons\n\n@mixin button-variant($color, $background, $border) {\n  $active-background: darken($background, 10%);\n  $active-border: darken($border, 12%);\n\n  color: $color;\n  background-color: $background;\n  border-color: $border;\n  @include box-shadow($btn-box-shadow);\n\n  @include hover {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n  }\n\n  &:focus,\n  &.focus {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n  }\n\n  &:active,\n  &.active,\n  .open > &.dropdown-toggle {\n    color: $color;\n    background-color: $active-background;\n        border-color: $active-border;\n    // Remove the gradient for the pressed/active state\n    background-image: none;\n    @include box-shadow($btn-active-box-shadow);\n\n    &:hover,\n    &:focus,\n    &.focus {\n      color: $color;\n      background-color: darken($background, 17%);\n          border-color: darken($border, 25%);\n    }\n  }\n\n  &.disabled,\n  &:disabled {\n    &:focus,\n    &.focus {\n      background-color: $background;\n          border-color: $border;\n    }\n    @include hover {\n      background-color: $background;\n          border-color: $border;\n    }\n  }\n}\n\n@mixin button-outline-variant($color) {\n  color: $color;\n  background-image: none;\n  background-color: transparent;\n  border-color: $color;\n\n  @include hover {\n    color: #fff;\n    background-color: $color;\n        border-color: $color;\n  }\n\n  &:focus,\n  &.focus {\n    color: #fff;\n    background-color: $color;\n        border-color: $color;\n  }\n\n  &:active,\n  &.active,\n  .open > &.dropdown-toggle {\n    color: #fff;\n    background-color: $color;\n        border-color: $color;\n\n    &:hover,\n    &:focus,\n    &.focus {\n      color: #fff;\n      background-color: darken($color, 17%);\n          border-color: darken($color, 25%);\n    }\n  }\n\n  &.disabled,\n  &:disabled {\n    &:focus,\n    &.focus {\n      border-color: lighten($color, 20%);\n    }\n    @include hover {\n      border-color: lighten($color, 20%);\n    }\n  }\n}\n\n// Button sizes\n@mixin button-size($padding-y, $padding-x, $font-size, $border-radius) {\n  padding: $padding-y $padding-x;\n  font-size: $font-size;\n  @include border-radius($border-radius);\n}\n",".fade {\n  opacity: 0;\n  transition: opacity .15s linear;\n\n  &.in {\n    opacity: 1;\n  }\n}\n\n.collapse {\n  display: none;\n  &.in {\n    display: block;\n  }\n}\n\ntr {\n  &.collapse.in {\n    display: table-row;\n  }\n}\n\ntbody {\n  &.collapse.in {\n    display: table-row-group;\n  }\n}\n\n.collapsing {\n  position: relative;\n  height: 0;\n  overflow: hidden;\n  transition-timing-function: ease;\n  transition-duration: .35s;\n  transition-property: height;\n}\n","// The dropdown wrapper (`<div>`)\n.dropup,\n.dropdown {\n  position: relative;\n}\n\n.dropdown-toggle {\n  // Generate the caret automatically\n  &::after {\n    display: inline-block;\n    width: 0;\n    height: 0;\n    margin-left: $caret-width;\n    vertical-align: middle;\n    content: \"\";\n    border-top: $caret-width solid;\n    border-right: $caret-width solid transparent;\n    border-left: $caret-width solid transparent;\n  }\n\n  // Prevent the focus on the dropdown toggle when closing dropdowns\n  &:focus {\n    outline: 0;\n  }\n}\n\n.dropup {\n  .dropdown-toggle {\n    &::after {\n      border-top: 0;\n      border-bottom: $caret-width solid;\n    }\n  }\n}\n\n// The dropdown menu\n.dropdown-menu {\n  position: absolute;\n  top: 100%;\n  left: 0;\n  z-index: $zindex-dropdown;\n  display: none; // none by default, but block on \"open\" of the menu\n  float: left;\n  min-width: $dropdown-min-width;\n  padding: $dropdown-padding-y 0;\n  margin: $dropdown-margin-top 0 0; // override default ul\n  font-size: $font-size-base;\n  color: $body-color;\n  text-align: left; // Ensures proper alignment if parent has it changed (e.g., modal footer)\n  list-style: none;\n  background-color: $dropdown-bg;\n  background-clip: padding-box;\n  border: $dropdown-border-width solid $dropdown-border-color;\n  @include border-radius($border-radius);\n  @include box-shadow($dropdown-box-shadow);\n}\n\n// Dividers (basically an `<hr>`) within the dropdown\n.dropdown-divider {\n  @include nav-divider($dropdown-divider-bg);\n}\n\n// Links, buttons, and more within the dropdown menu\n//\n// `<button>`-specific styles are denoted with `// For <button>s`\n.dropdown-item {\n  display: block;\n  width: 100%; // For `<button>`s\n  padding: 3px $dropdown-item-padding-x;\n  clear: both;\n  font-weight: normal;\n  color: $dropdown-link-color;\n  text-align: inherit; // For `<button>`s\n  white-space: nowrap; // prevent links from randomly breaking onto new lines\n  background: none; // For `<button>`s\n  border: 0; // For `<button>`s\n\n  @include hover-focus {\n    color: $dropdown-link-hover-color;\n    text-decoration: none;\n    background-color: $dropdown-link-hover-bg;\n  }\n\n  // Active state\n  &.active {\n    @include plain-hover-focus {\n      color: $dropdown-link-active-color;\n      text-decoration: none;\n      background-color: $dropdown-link-active-bg;\n      outline: 0;\n    }\n  }\n\n  // Disabled state\n  //\n  // Gray out text and ensure the hover/focus state remains gray\n  &.disabled {\n    @include plain-hover-focus {\n      color: $dropdown-link-disabled-color;\n    }\n\n    // Nuke hover/focus effects\n    @include hover-focus {\n      text-decoration: none;\n      cursor: $cursor-disabled;\n      background-color: transparent;\n      background-image: none; // Remove CSS gradient\n      @include reset-filter();\n    }\n  }\n}\n\n// Open state for the dropdown\n.open {\n  // Show the menu\n  > .dropdown-menu {\n    display: block;\n  }\n\n  // Remove the outline when :focus is triggered\n  > a {\n    outline: 0;\n  }\n}\n\n// Menu positioning\n//\n// Add extra class to `.dropdown-menu` to flip the alignment of the dropdown\n// menu with the parent.\n.dropdown-menu-right {\n  right: 0;\n  left: auto; // Reset the default from `.dropdown-menu`\n}\n\n.dropdown-menu-left {\n  right: auto;\n  left: 0;\n}\n\n// Dropdown section headers\n.dropdown-header {\n  display: block;\n  padding: $dropdown-padding-y $dropdown-item-padding-x;\n  margin-bottom: 0; // for use with heading elements\n  font-size: $font-size-sm;\n  color: $dropdown-header-color;\n  white-space: nowrap; // as with > li > a\n}\n\n// Backdrop to catch body clicks on mobile, etc.\n.dropdown-backdrop {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-dropdown-backdrop;\n}\n\n// Allow for dropdowns to go bottom up (aka, dropup-menu)\n//\n// Just add .dropup after the standard .dropdown class and you're