   }\n  }\n\n  &::before {\n    position: absolute;\n    top: -$custom-file-border-width;\n    right: -$custom-file-border-width;\n    bottom: -$custom-file-border-width;\n    z-index: 6;\n    display: block;\n    height: $custom-file-height;\n    padding: $custom-file-padding-x $custom-file-padding-y;\n    line-height: $custom-file-line-height;\n    color: $custom-file-button-color;\n    background-color: $custom-file-button-bg;\n    border: $custom-file-border-width solid $custom-file-border-color;\n    @include border-radius(0 $custom-file-border-radius $custom-file-border-radius 0);\n  }\n\n  @each $lang, $text in map-get($custom-file-text, button-label) {\n    &:lang(#{$lang})::before {\n      content: $text;\n    }\n  }\n}\n","// Base class\n//\n// Kickstart any navigation component with a set of style resets. Works with\n// `<nav>`s or `<ul>`s.\n\n.nav {\n  padding-left: 0;\n  margin-bottom: 0;\n  list-style: none;\n}\n\n.nav-link {\n  display: inline-block;\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n\n  // Disabled state lightens text and removes hover/tab effects\n  &.disabled {\n    color: $nav-disabled-link-color;\n\n    @include plain-hover-focus {\n      color: $nav-disabled-link-hover-color;\n      cursor: $cursor-disabled;\n      background-color: $nav-disabled-link-hover-bg;\n    }\n  }\n}\n\n\n// Nav inline\n\n.nav-inline {\n  .nav-item {\n    display: inline-block;\n  }\n\n  .nav-item + .nav-item,\n  .nav-link + .nav-link {\n    margin-left: $nav-item-inline-spacer;\n  }\n}\n\n\n//\n// Tabs\n//\n\n.nav-tabs {\n  border-bottom: $nav-tabs-border-width solid $nav-tabs-border-color;\n  @include clearfix();\n\n  .nav-item {\n    float: left;\n    // Make the list-items overlay the bottom border\n    margin-bottom: -$nav-tabs-border-width;\n\n    + .nav-item {\n      margin-left: $nav-item-margin;\n    }\n  }\n\n  .nav-link {\n    display: block;\n    padding: $nav-link-padding;\n    border: $nav-tabs-border-width solid transparent;\n    @include border-top-radius($nav-tabs-border-radius);\n\n    @include hover-focus {\n      border-color: $nav-tabs-link-hover-border-color $nav-tabs-link-hover-border-color $nav-tabs-border-color;\n    }\n\n    &.disabled {\n      @include plain-hover-focus {\n        color: $nav-disabled-link-color;\n        background-color: transparent;\n        border-color: transparent;\n      }\n    }\n  }\n\n  .nav-link.active,\n  .nav-item.open .nav-link {\n    @include plain-hover-focus {\n      color: $nav-tabs-active-link-hover-color;\n      background-color: $nav-tabs-active-link-hover-bg;\n      border-color: $nav-tabs-active-link-hover-border-color $nav-tabs-active-link-hover-border-color transparent;\n    }\n  }\n\n  .dropdown-menu {\n    // Make dropdown border overlap tab border\n    margin-top: -$nav-tabs-border-width;\n    // Remove the top rounded corners here since there is a hard edge above the menu\n    @include border-top-radius(0);\n  }\n}\n\n\n//\n// Pills\n//\n\n.nav-pills {\n  @include clearfix();\n\n  .nav-item {\n    float: left;\n\n    + .nav-item {\n      margin-left: $nav-item-margin;\n    }\n  }\n\n  .nav-link {\n    display: block;\n    padding: $nav-link-padding;\n    @include border-radius($nav-pills-border-radius);\n  }\n\n  .nav-link.active,\n  .nav-item.open .nav-link {\n    @include plain-hover-focus {\n      color: $nav-pills-active-link-color;\n      cursor: default;\n      background-color: $nav-pills-active-link-bg;\n    }\n  }\n}\n\n.nav-stacked {\n  .nav-item {\n    display: block;\n    float: none;\n\n    + .nav-item {\n      margin-top: $nav-item-margin;\n      margin-left: 0;\n    }\n  }\n}\n\n\n//\n// Tabbable tabs\n//\n\n// Hide tabbable panes to start, show them when `.active`\n.tab-content {\n  > .tab-pane {\n    display: none;\n  }\n  > .active {\n    display: block;\n  }\n}\n","// Wrapper and base class\n//\n// Provide a static navbar from which we expand to create full-width, fixed, and\n// other navbar variations.\n\n.navbar {\n  position: relative;\n  padding: $navbar-padding-y $navbar-padding-x;\n  @include clearfix;\n\n  @include media-breakpoint-up(sm) {\n    @include border-radius($navbar-border-radius);\n  }\n}\n\n\n// Navbar alignment options\n//\n// Display the navbar across the entirety of the page or fixed it to the top or\n// bottom of the page.\n\n// A static, full width modifier with no rounded corners.\n.navbar-full {\n  z-index: $zindex-navbar;\n\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n// Fix the top/bottom navbars when screen real estate supports it\n.navbar-fixed-top,\n.navbar-fixed-bottom {\n  position: fixed;\n  right: 0;\n  left: 0;\n  z-index: $zindex-navbar-fixed;\n\n  // Undo the rounded corners\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n.navbar-fixed-top {\n  top: 0;\n}\n\n.navbar-fixed-bottom {\n  bottom: 0;\n}\n\n.navbar-sticky-top {\n  position: sticky;\n  top: 0;\n  z-index: $zindex-navbar-sticky;\n  width: 100%;\n\n  // Undo the rounded corners\n  @include media-breakpoint-up(sm) {\n    @include border-radius(0);\n  }\n}\n\n\n//\n// Brand/project name\n//\n\n.navbar-brand {\n  float: left;\n  padding-top: $navbar-brand-padding-y;\n  padding-bottom: $navbar-brand-padding-y;\n  margin-right: 1rem;\n  font-size: $font-size-lg;\n  line-height: inherit;\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n}\n\n\n.navbar-divider {\n  float: left;\n  width: $border-width;\n  padding-top: $navbar-divider-padding-y;\n  padding-bottom: $navbar-divider-padding-y;\n  margin-right: $navbar-padding-x;\n  margin-left:  $navbar-padding-x;\n  overflow: hidden;\n\n  &::before {\n    content: \"\\00a0\";\n  }\n}\n\n\n// Navbar text\n//\n//\n\n.navbar-text {\n  display: inline-block;\n  padding-top:    .425rem;\n  padding-bottom: .425rem;\n}\n\n\n// Navbar toggle\n//\n// Custom button for toggling the `.navbar-collapse`, powered by the collapse\n// Bootstrap JavaScript plugin.\n\n.navbar-toggler {\n  width: 2.5em;\n  height: 2em;\n  padding: $navbar-toggler-padding-y $navbar-toggler-padding-x;\n  font-size: $navbar-toggler-font-size;\n  line-height: 1;\n  background: transparent no-repeat center center;\n  background-size: 24px 24px;\n  border: $border-width solid transparent;\n  @include border-radius($navbar-toggler-border-radius);\n\n  @include hover-focus {\n    text-decoration: none;\n  }\n}\n\n// scss-lint:disable ImportantRule\n.navbar-toggleable {\n  @each $breakpoint in map-keys($grid-breakpoints) {\n    $next: breakpoint-next($breakpoint, $grid-breakpoints);\n\n    &-#{$breakpoint} {\n      @include clearfix;\n\n      @include media-breakpoint-down($breakpoint) {\n        .navbar-brand {\n          display: block;\n          float: none;\n          margin-top: .5rem;\n          margin-right: 0;\n        }\n\n        .navbar-nav {\n          margin-top: .5rem;\n          margin-bottom: .5rem;\n\n          .dropdown-menu {\n            position: static;\n            float: none;\n          }\n        }\n      }\n\n      @include media-breakpoint-up($next) {\n        display: block;\n      }\n    }\n  }\n}\n// scss-lint:enable ImportantRule\n\n\n// Navigation\n//\n// Custom navbar navigation built on the base `.nav` styles.\n\n.navbar-nav {\n  .nav-item {\n    float: left;\n  }\n\n  .nav-link {\n    display: block;\n    padding-top:    .425rem;\n    padding-bottom: .425rem;\n\n    + .nav-link {\n      margin-left: 1rem;\n    }\n  }\n\n  .nav-item + .nav-item {\n    margin-left: 1rem;\n  }\n}\n\n// Dark links against a light background\n.navbar-light {\n  .navbar-brand,\n  .navbar-toggler {\n    color: $navbar-light-active-color;\n\n    @include hover-focus {\n      color: $navbar-light-active-color;\n    }\n  }\n\n  .navbar-nav {\n    .nav-link {\n      color: $navbar-light-color;\n\n      @include hover-focus {\n        color: $navbar-light-hover-color;\n      }\n    }\n\n    .open > .nav-link,\n    .active > .nav-link,\n    .nav-link.open,\n    .nav-link.active {\n      @include plain-hover-focus {\n        color: $navbar-light-active-color;\n      }\n    }\n  }\n\n  .navbar-toggler {\n    background-image: $navbar-light-toggler-bg;\n    border-color: $navbar-light-toggler-border;\n  }\n\n  .navbar-divider {\n    background-color: rgba(0,0,0,.075);\n  }\n}\n\n// White links against a dark background\n.navbar-dark {\n  .navbar-brand,\n  .navbar-toggler {\n    color: $navbar-dark-active-color;\n\n    @include hover-focus {\n      color: $navbar-dark-active-color;\n    }\n  }\n\n  .navbar-nav {\n    .nav-link {\n      color: $navbar-dark-color;\n\n      @include hover-focus {\n        color: $navbar-dark-hover-color;\n      }\n    }\n\n    .open > .nav-link,\n    .active > .nav-link,\n    .nav-link.open,\n    .nav-link.active {\n      @include plain-hover-focus {\n        color: $navbar-dark-active-color;\n      }\n    }\n  }\n\n  .navbar-toggler {\n    background-image: $navbar-dark-toggler-bg;\n    border-color: $navbar-dark-toggler-border;\n  }\n\n  .navbar-divider {\n    background-color: rgba(255,255,255,.075);\n  }\n}\n\n\n// Navbar toggleable\n//\n// Custom override for collapse plugin in navbar.\n\n.navbar-toggleable {\n  &-xs {\n    @include clearfix;\n    @include media-breakpoint-down(xs) {\n      .navbar-nav .nav-item {\n        float: none;\n        margin-left: 0;\n      }\n    }\n    @include media-breakpoint-up(sm) {\n      display: block !important;\n    }\n  }\n\n  &-sm {\n    @include clearfix;\n    @include media-breakpoint-down(sm) {\n      .navbar-nav .nav-item {\n        float: none;\n        margin-left: 0;\n      }\n    }\n    @include media-breakpoint-up(md) {\n      display: block !important;\n    }\n  }\n\n  &-md {\n    @include clearfix;\n    @include media-breakpoint-down(md) {\n      .navbar-nav .nav-item {\n        float: none;\n        margin-left: 0;\n      }\n    }\n    @include media-breakpoint-up(lg) {\n      display: block !important;\n    }\n  }\n}\n","//\n// Base styles\n//\n\n.card {\n  position: relative;\n  display: block;\n  margin-bottom: $card-spacer-y;\n  background-color: $card-bg;\n  // border: $card-border-width solid $card-border-color;\n  @include border-radius($card-border-radius);\n  border: $card-border-width solid $card-border-color;\n}\n\n.card-block {\n  @include clearfix;\n  padding: $card-spacer-x;\n}\n\n.card-title {\n  margin-bottom: $card-spacer-y;\n}\n\n.card-subtitle {\n  margin-top: -($card-spacer-y / 2);\n  margin-bottom: 0;\n}\n\n.card-text:last-child {\n  margin-bottom: 0;\n}\n\n// .card-actions {\n//   padding: $card-spacer-y $card-spacer-x;\n\n//   .card-link + .card-link {\n//     margin-left: $card-spacer-x;\n//   }\n// }\n\n.card-link {\n  @include hover {\n    text-decoration: none;\n  }\n\n  + .card-link {\n    margin-left: $card-spacer-x;\n  }\n}\n\n.card {\n  > .list-group:first-child {\n    .list-group-item:first-child {\n      @include border-top-radius($card-border-radius);\n    }\n  }\n\n  > .list-group:last-child {\n    .list-group-item:last-child {\n      @include border-bottom-radius($card-border-radius);\n    }\n  }\n}\n\n\n//\n// Optional textual caps\n//\n\n.card-header {\n  @include clearfix;\n  padding: $card-spacer-y $card-spacer-x;\n  margin-bottom: 0; // Removes the default margin-bottom of <hN>\n  background-color: $card-cap-bg;\n  border-bottom: $card-border-width solid $card-border-color;\n\n  &:first-child {\n    @include border-radius($card-border-radius-inner $card-border-radius-inner 0 0);\n  }\n}\n\n.card-footer {\n  @include clearfix;\n  padding: $card-spacer-y $card-spacer-x;\n  background-color: $card-cap-bg;\n  border-top: $card-border-width solid $card-border-color;\n\n  &:last-child {\n    @include border-radius(0 0 $card-border-radius-inner $card-border-radius-inner);\n  }\n}\n\n\n//\n// Header navs\n//\n\n.card-header-tabs {\n  margin-right: -($card-spacer-x / 2);\n  margin-bottom: -$card-spacer-y;\n  margin-left: -($card-spacer-x / 2);\n  border-bottom: 0;\n}\n\n.card-header-pills {\n  margin-right: -($card-spacer-x / 2);\n  margin-left: -($card-spacer-x / 2);\n}\n\n\n//\n// Background variations\n//\n\n.card-primary {\n  @include card-variant($brand-primary, $brand-primary);\n}\n.card-success {\n  @include card-variant($brand-success, $brand-success);\n}\n.card-info {\n  @include card-variant($brand-info, $brand-info);\n}\n.card-warning {\n  @include card-variant($brand-warning, $brand-warning);\n}\n.card-danger {\n  @include card-variant($brand-danger, $brand-danger);\n}\n\n// Remove all backgrounds\n.card-outline-primary {\n  @include card-outline-variant($btn-primary-bg);\n}\n.card-outline-secondary {\n  @include card-outline-variant($btn-secondary-border);\n}\n.card-outline-info {\n  @include card-outline-variant($btn-info-bg);\n}\n.card-outline-success {\n  @include card-outline-variant($btn-success-bg);\n}\n.card-outline-warning {\n  @include card-outline-variant($btn-warning-bg);\n}\n.card-outline-danger {\n  @include card-outline-variant($btn-danger-bg);\n}\n\n//\n// Inverse text within a card for use with dark backgrounds\n//\n\n.card-inverse {\n  @include card-inverse;\n}\n\n//\n// Blockquote\n//\n\n.card-blockquote {\n  padding: 0;\n  margin-bottom: 0;\n  border-left: 0;\n}\n\n// Card image\n.card-img {\n  // margin: -1.325rem;\n  @include border-radius($card-border-radius-inner);\n}\n.card-img-overlay {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  padding: $card-img-overlay-padding;\n}\n\n\n\n// Card image caps\n.card-img-top {\n  @include border-top-radius($card-border-radius-inner);\n}\n.card-img-bottom {\n  @include border-bottom-radius($card-border-radius-inner);\n}\n\n\n// Card set\n//\n// Heads up! We do some funky style resetting here for margins across our two\n// variations (one flex, one table). Individual cards have margin-bottom by\n// default, but they're ignored due to table styles. For a consistent design,\n// we've done the same to the flex variation.\n//\n// Those changes are noted by `// Margin balancing`.\n\n@if $enable-flex {\n  @include media-breakpoint-up(sm) {\n    .card-deck {\n      display: flex;\n      flex-flow: row wrap;\n      margin-right: -$card-deck-margin;\n      margin-bottom: $card-spacer-y; // Margin balancing\n      margin-left: -$card-deck-margin;\n\n      .card {\n        flex: 1 0 0;\n        margin-right: $card-deck-margin;\n        margin-bottom: 0; // Margin balancing\n        margin-left: $card-deck-margin;\n      }\n    }\n  }\n} @else {\n  @include media-breakpoint-up(sm) {\n    $space-between-cards: (2 * $card-deck-margin);\n    .card-deck {\n      display: table;\n      width: 100%;\n      margin-bottom: $card-spacer-y; // Margin balancing\n      table-layout: fixed;\n      border-spacing: $space-between-cards 0;\n\n      .card {\n        display: table-cell;\n        margin-bottom: 0; // Margin balancing\n        vertical-align: top;\n      }\n    }\n    .card-deck-wrapper {\n      margin-right: (-$space-between-cards);\n      margin-left: (-$space-between-cards);\n    }\n  }\n}\n\n//\n// Card groups\n//\n\n@include media-breakpoint-up(sm) {\n  .card-group {\n    @if $enable-flex {\n      display: flex;\n      flex-flow: row wrap;\n    } @else {\n      display: table;\n      width: 100%;\n      table-layout: fixed;\n    }\n\n    .card {\n      @if $enable-flex {\n        flex: 1 0 0;\n      } @else {\n        display: table-cell;\n        vertical-align: top;\n      }\n\n      + .card {\n        margin-left: 0;\n        border-left: 0;\n      }\n\n      // Handle rounded corners\n      @if $enable-rounded {\n        &:first-child {\n          @include border-right-radius(0);\n\n          .card-img-top {\n            border-top-right-radius: 0;\n          }\n          .card-img-bottom {\n            border-bottom-right-radius: 0;\n          }\n        }\n        &:last-child {\n          @include border-left-radius(0);\n\n          .card-img-top {\n            border-top-left-radius: 0;\n          }\n          .card-img-bottom {\n            border-bottom-left-radius: 0;\n          }\n        }\n\n        &:not(:first-child):not(:last-child) {\n          border-radius: 0;\n\n          .card-img-top,\n          .card-img-bottom {\n            border-radius: 0;\n          }\n        }\n      }\n    }\n  }\n}\n\n\n//\n// Card\n//\n\n@include media-breakpoint-up(sm) {\n  .card-columns {\n    column-count: 3;\n    column-gap: $card-columns-sm-up-column-gap;\n\n    .card {\n      display: inline-block; // Don't let them vertically span multiple columns\n      width: 100%; // Don't let them exceed the column width\n    }\n  }\n}\n","// Card variants\n\n@mixin card-variant($background, $border) {\n  background-color: $background;\n  border-color: $border;\n\n  .card-header,\n  .card-footer {\n    background-color: transparent;\n  }\n}\n\n@mixin card-outline-variant($color) {\n  background-color: transparent;\n  border-color: $color;\n}\n\n//\n// Inverse text within a card for use with dark backgrounds\n//\n\n@mixin card-inverse {\n  .card-header,\n  .card-footer {\n    border-color: rgba(255,255,255,.2);\n  }\n  .card-header,\n  .card-footer,\n  .card-title,\n  .card-blockquote {\n    color: #fff;\n  }\n  .card-link,\n  .card-text,\n  .card-subtitle,\n  .card-blockquote .blockquote-footer {\n    color: rgba(255,255,255,.65);\n  }\n  .card-link {\n    @include hover-focus {\n      color: $card-link-hover-color;\n    }\n  }\n}\n",".breadcrumb {\n  padding: $breadcrumb-padding-y $breadcrumb-padding-x;\n  margin-bottom: $spacer-y;\n  list-style: none;\n  background-color: $breadcrumb-bg;\n  @include border-radius($border-radius);\n  @include clearfix;\n}\n\n.breadcrumb-item {\n  float: left;\n\n  // The separator between breadcrumbs (by default, a forward-slash: \"/\")\n  + .breadcrumb-item::before {\n    display: inline-block; // Suppress underlining of the separator in modern browsers\n    padding-right: $breadcrumb-item-padding;\n    padding-left: $breadcrumb-item-padding;\n    color: $breadcrumb-divider-color;\n    content: \"#{$breadcrumb-divider}\";\n  }\n\n  // IE9-11 hack to properly handle hyperlink underlines for breadcrumbs built\n  // without `<ul>`s. The `::before` pseudo-element generates an element\n  // *within* the .breadcrumb-item and thereby inherits the `text-decoration`.\n  //\n  // To trick IE into suppressing the underline, we give the pseudo-element an\n  // underline and then immediately remove it.\n  + .breadcrumb-item:hover::before {\n    text-decoration: underline;\n  }\n  + .breadcrumb-item:hover::before {\n    text-decoration: none;\n  }\n\n  &.active {\n    color: $breadcrumb-active-color;\n  }\n}\n",".pagination {\n  display: inline-block;\n  padding-left: 0;\n  margin-top: $spacer-y;\n  margin-bottom: $spacer-y;\n  @include border-radius();\n}\n\n.page-item {\n  display: inline; // Remove list-style and block-level defaults\n\n  &:first-child {\n    .page-link {\n      margin-left: 0;\n      @include border-left-radius($border-radius);\n    }\n  }\n  &:last-child {\n    .page-link {\n      @include border-right-radius($border-radius);\n    }\n  }\n\n  &.active .page-link {\n    @include plain-hover-focus {\n      z-index: 2;\n      color: $pagination-active-color;\n      cursor: default;\n      background-color: $pagination-active-bg;\n      border-color: $pagination-active-border;\n    }\n  }\n\n  &.disabled .page-link {\n    @include plain-hover-focus {\n      color: $pagination-disabled-color;\n      pointer-events: none;\n      cursor: $cursor-disabled;\n      background-color: $pagination-disabled-bg;\n      border-color: $pagination-disabled-border;\n    }\n  }\n}\n\n.page-link {\n  position: relative;\n  float: left; // Collapse white-space\n  padding: $pagination-padding-y $pagination-padding-x;\n  margin-left: -1px;\n  color: $pagination-color;\n  text-decoration: none;\n  background-color: $pagination-bg;\n  border: $pagination-border-width solid $pagination-border-color;\n\n  @include hover-focus {\n    color: $pagination-hover-color;\n    background-color: $pagination-hover-bg;\n    border-color: $pagination-hover-border;\n  }\n}\n\n\n//\n// Sizing\n//\n\n.pagination-lg {\n  @include pagination-size($pagination-padding-y-lg, $pagination-padding-x-lg, $font-size-lg, $line-height-lg, $border-radius-lg);\n}\n\n.pagination-sm {\n  @include pagination-size($pagination-padding-y-sm, $pagination-padding-x-sm, $font-size-sm, $line-height-sm, $border-radius-sm);\n}\n","// Pagination\n\n@mixin pagination-size($padding-y, $padding-x, $font-size, $line-height, $border-radius) {\n  .page-link {\n    padding: $padding-y $padding-x;\n    font-size: $font-size;\n  }\n\n  .page-item {\n    &:first-child {\n      .page-link {\n        @include border-left-radius($border-radius);\n      }\n    }\n    &:last-child {\n      .page-link {\n        @include border-right-radius($border-radius);\n      }\n    }\n  }\n}\n","// Base class\n//\n// Requires one of the contextual, color modifier classes for `color` and\n// `background-color`.\n\n.tag {\n  display: inline-block;\n  padding: $tag-padding-y $tag-padding-x;\n  font-size: $tag-font-size;\n  font-weight: $tag-font-weight;\n  line-height: 1;\n  color: $tag-color;\n  text-align: center;\n  white-space: nowrap;\n  vertical-align: baseline;\n  @include border-radius();\n\n  // Empty tags collapse automatically\n  &:empty {\n    display: none;\n  }\n}\n\n// Quick fix for tags in buttons\n.btn .tag {\n  position: relative;\n  top: -1px;\n}\n\n// scss-lint:disable QualifyingElement\n// Add hover effects, but only for links\na.tag {\n  @include hover-focus {\n    color: $tag-link-hover-color;\n    text-decoration: none;\n    cursor: pointer;\n  }\n}\n// scss-lint:enable QualifyingElement\n\n// Pill tags\n//\n// Make them extra rounded with a modifier to replace v3's badges.\n\n.tag-pill {\n  padding-right: $tag-pill-padding-x;\n  padding-left: $tag-pill-padding-x;\n  @include border-radius($tag-pill-border-radius);\n}\n\n// Colors\n//\n// Contextual variations (linked tags get darker on :hover).\n\n.tag-default {\n  @include tag-variant($tag-default-bg);\n}\n\n.tag-primary {\n  @include tag-variant($tag-primary-bg);\n}\n\n.tag-success {\n  @include tag-variant($tag-success-bg);\n}\n\n.tag-info {\n  @include tag-variant($tag-info-bg);\n}\n\n.tag-warning {\n  @include tag-variant($tag-warning-bg);\n}\n\n.tag-danger {\n  @include tag-variant($tag-danger-bg);\n}\n","// Tags\n\n@mixin tag-variant($color) {\n  background-color: $color;\n\n  &[href] {\n    @include hover-focus {\n      background-color: darken($color, 10%);\n    }\n  }\n}\n",".jumbotron {\n  padding: $jumbotron-padding ($jumbotron-padding / 2);\n  margin-bottom: $jumbotron-padding;\n  background-color: $jumbotron-bg;\n  @include border-radius($border-radius-lg);\n\n  @include media-breakpoint-up(sm) {\n    padding: ($jumbotron-padding * 2) $jumbotron-padding;\n  }\n}\n\n.jumbotron-hr {\n  border-top-color: darken($jumbotron-bg, 10%);\n}\n\n.jumbotron-fluid {\n  padding-right: 0;\n  padding-left: 0;\n  @include border-radius(0);\n}\n","//\n// Base styles\n//\n\n.alert {\n  padding: $alert-padding-y $alert-padding-x;\n  margin-bottom: $spacer-y;\n  border: $alert-border-width solid transparent;\n  @include border-radius($alert-border-radius);\n}\n\n// Headings for larger alerts\n.alert-heading {\n  // Specified to prevent conflicts of changing $headings-color\n  color: inherit;\n}\n\n// Provide class for links that match alerts\n.alert-link {\n  font-weight: $alert-link-font-weight;\n}\n\n\n// Dismissible alerts\n//\n// Expand the right padding and account for the close button's positioning.\n\n.alert-dismissible {\n  padding-right: ($alert-padding-x * 2);\n\n  // Adjust close link position\n  .close {\n    position: relative;\n    top: -.125rem;\n    right: -$alert-padding-x;\n    color: inherit;\n  }\n}\n\n\n// Alternate styles\n//\n// Generate contextual modifier classes for colorizing the alert.\n\n.alert-success {\n  @include alert-variant($alert-success-bg, $alert-success-border, $alert-success-text);\n}\n.alert-info {\n  @include alert-variant($alert-info-bg, $alert-info-border, $alert-info-text);\n}\n.alert-warning {\n  @include alert-variant($alert-warning-bg, $alert-warning-border, $alert-warning-text);\n}\n.alert-danger {\n  @include alert-variant($alert-danger-bg, $alert-danger-border, $alert-danger-text);\n}\n","// Alerts\n\n@mixin alert-variant($background, $border, $body-color) {\n  background-color: $background;\n  border-color: $border;\n  color: $body-color;\n\n  hr {\n    border-top-color: darken($border, 5%);\n  }\n  .alert-link {\n    color: darken($body-color, 10%);\n  }\n}\n","//\n// Progress animations\n//\n\n@keyframes progress-bar-stripes {\n  from { background-position: $spacer-y 0; }\n  to { background-position: 0 0; }\n}\n\n\n//\n// Basic progress bar\n//\n\n.progress {\n  display: block;\n  width: 100%;\n  height: $spacer-y; // todo: make a new var for this\n  margin-bottom: $spacer-y;\n}\n.progress[value] {\n  // Set overall background\n  background-color: $progress-bg;\n  // Remove Firefox and Opera border\n  border: 0;\n  // Reset the default appearance\n  appearance: none;\n  // Set overall border radius\n  @include border-radius($progress-border-radius);\n}\n\n// Filled-in portion of the bar\n.progress[value]::-ms-fill {\n  background-color: $progress-bar-color;\n  // Remove right-hand border of value bar from IE10+/Edge\n  border: 0;\n}\n.progress[value]::-moz-progress-bar {\n  background-color: $progress-bar-color;\n  @include border-left-radius($progress-border-radius);\n}\n.progress[value]::-webkit-progress-value {\n  background-color: $progress-bar-color;\n  @include border-left-radius($progress-border-radius);\n}\n// Tweaks for full progress bar\n.progress[value=\"100\"]::-moz-progress-bar {\n  @include border-right-radius($progress-border-radius);\n}\n.progress[value=\"100\"]::-webkit-progress-value {\n  @include border-right-radius($progress-border-radius);\n}\n\n// Unfilled portion of the bar\n.progress[value]::-webkit-progress-bar {\n  background-color: $progress-bg;\n  @include border-radius($progress-border-radius);\n  @include box-shadow($progress-box-shadow);\n}\nbase::-moz-progress-bar, // Absurd-but-syntactically-valid selector to make these styles Firefox-only\n.progress[value] {\n  background-color: $progress-bg;\n  @include border-radius($progress-border-radius);\n  @include box-shadow($progress-box-shadow);\n}\n\n// IE9 hacks to accompany custom markup. We don't need to scope this via media queries, but I feel better doing it anyway.\n@media screen and (min-width:0\\0) {\n  .progress {\n    background-color: $progress-bg;\n    @include border-radius($progress-border-radius);\n    @include box-shadow($progress-box-shadow);\n  }\n  .progress-bar {\n    display: inline-block;\n    height: $spacer-y;\n    text-indent: -999rem; // Simulate hiding of value as in native `<progress>`\n    background-color: $progress-bar-color;\n    @include border-left-radius($progress-border-radius);\n  }\n  .progress[width=\"100%\"] {\n    @include border-right-radius($progress-border-radius);\n  }\n}\n\n\n//\n// Striped\n//\n\n.progress-striped[value]::-webkit-progress-value {\n  @include gradient-striped();\n  background-size: $spacer-y $spacer-y;\n}\n.progress-striped[value]::-moz-progress-bar {\n  @include gradient-striped();\n  background-size: $spacer-y $spacer-y;\n}\n.progress-striped[value]::-ms-fill {\n  @include gradient-striped();\n  background-size: $spacer-y $spacer-y;\n}\n// IE9\n@media screen and (min-width:0\\0) {\n  .progress-bar-striped {\n    @include gradient-striped();\n    background-size: $spacer-y $spacer-y;\n  }\n}\n\n\n//\n// Animated\n//\n\n.progress-animated[value]::-webkit-progress-value {\n  animation: progress-bar-stripes 2s linear infinite;\n}\n.progress-animated[value]::-moz-progress-bar {\n  animation: progress-bar-stripes 2s linear infinite;\n}\n// IE9\n@media screen and (min-width:0\\0) {\n  .progress-animated .progress-bar-striped {\n    animation: progress-bar-stripes 2s linear infinite;\n  }\n}\n\n\n//\n// Variations\n//\n\n.progress-success {\n  @include progress-variant($progress-bar-success-bg);\n}\n.progress-info {\n  @include progress-variant($progress-bar-info-bg);\n}\n.progress-warning {\n  @include progress-variant($progress-bar-warning-bg);\n}\n.progress-danger {\n  @include progress-variant($progress-bar-danger-bg);\n}\n","// Gradients\n\n// Horizontal gradient, from left to right\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n// Color stops are not available in IE9.\n@mixin gradient-x($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to right, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1); // IE9\n}\n\n// Vertical gradient, from top to bottom\n//\n// Creates two color stops, start and end, by specifying a color and position for each color stop.\n// Color stops are not available in IE9.\n@mixin gradient-y($start-color: #555, $end-color: #333, $start-percent: 0%, $end-percent: 100%) {\n  background-image: linear-gradient(to bottom, $start-color $start-percent, $end-color $end-percent);\n  background-repeat: repeat-x;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=0); // IE9\n}\n\n@mixin gradient-directional($start-color: #555, $end-color: #333, $deg: 45deg) {\n  background-repeat: repeat-x;\n  background-image: linear-gradient($deg, $start-color, $end-color);\n}\n@mixin gradient-x-three-colors($start-color: #00b3ee, $mid-color: #7a43b6, $color-stop: 50%, $end-color: #c3325f) {\n  background-image: linear-gradient(to right, $start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=1); // IE9 gets no color-stop at all for proper fallback\n}\n@mixin gradient-y-three-colors($start-color: #00b3ee, $mid-color: #7a43b6, $color-stop: 50%, $end-color: #c3325f) {\n  background-image: linear-gradient($start-color, $mid-color $color-stop, $end-color);\n  background-repeat: no-repeat;\n  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{ie-hex-str($start-color)}', endColorstr='#{ie-hex-str($end-color)}', GradientType=0); // IE9 gets no color-stop at all for proper fallback\n}\n@mixin gradient-radial($inner-color: #555, $outer-color: #333) {\n  background-image: radial-gradient(circle, $inner-color, $outer-color);\n  background-repeat: no-repeat;\n}\n@mixin gradient-striped($color: rgba(255,255,255,.15), $angle: 45deg) {\n  background-image: linear-gradient($angle, $color 25%, transparent 25%, transparent 50%, $color 50%, $color 75%, transparent 75%, transparent);\n}","// Progress bars\n\n@mixin progress-variant($color) {\n  &[value]::-webkit-progress-value {\n    background-color: $color;\n  }\n\n  &[value]::-moz-progress-bar {\n    background-color: $color;\n  }\n\n  // IE10+, Microsoft Edge\n  &[value]::-ms-fill {\n    background-color: $color;\n  }\n\n  // IE9\n  @media screen and (min-width:0\\0) {\n    .progress-bar {\n      background-color: $color;\n    }\n  }\n}\n","@if $enable-flex {\n  .media {\n    display: flex;\n  }\n  .media-body {\n    flex: 1;\n  }\n  .media-middle {\n    align-self: center;\n  }\n  .media-bottom {\n    align-self: flex-end;\n  }\n} @else {\n  .media,\n  .media-body {\n    overflow: hidden;\n  }\n  .media-body {\n    width: 10000px;\n  }\n  .media-left,\n  .media-right,\n  .media-body {\n    display: table-cell;\n    vertical-align: top;\n  }\n  .media-middle {\n    vertical-align: middle;\n  }\n  .media-bottom {\n    vertical-align: bottom;\n  }\n}\n\n\n//\n// Images/elements as the media anchor\n//\n\n.media-object {\n  display: block;\n\n  // Fix collapse in webkit from max-width: 100% and display: table-cell.\n  &.img-thumbnail {\n    max-width: none;\n  }\n}\n\n\n//\n// Alignment\n//\n\n.media-right {\n  padding-left: $media-alignment-padding-x;\n}\n\n.media-left {\n  padding-right: $media-alignment-padding-x;\n}\n\n\n//\n// Headings\n//\n\n.media-heading {\n  margin-top: 0;\n  margin-bottom: $media-heading-margin-bottom;\n}\n\n\n//\n// Media list variation\n//\n\n.media-list {\n  padding-left: 0;\n  list-style: none;\n}\n","// Base class\n//\n// Easily usable on <ul>, <ol>, or <div>.\n\n.list-group {\n  // No need to set list-style: none; since .list-group-item is block level\n  padding-left: 0; // reset padding because ul and ol\n  margin-bottom: 0;\n}\n\n\n// Individual list items\n//\n// Use on `li`s or `div`s within the `.list-group` parent.\n\n.list-group-item {\n  position: relative;\n  display: block;\n  padding: $list-group-item-padding-y $list-group-item-padding-x;\n  // Place the border on the list items and negative margin up for better styling\n  margin-bottom: -$list-group-border-width;\n  background-color: $list-group-bg;\n  border: $list-group-border-width solid $list-group-border-color;\n\n  &:first-child {\n    @include border-top-radius($list-group-border-radius);\n  }\n\n  &:last-child {\n    margin-bottom: 0;\n    @include border-bottom-radius($list-group-border-radius);\n  }\n\n  &.disabled {\n    @include plain-hover-focus {\n      color: $list-group-disabled-color;\n      cursor: $cursor-disabled;\n      background-color: $list-group-disabled-bg;\n\n      // Force color to inherit for custom content\n      .list-group-item-heading {\n        color: inherit;\n      }\n      .list-group-item-text {\n        color: $list-group-disabled-text-color;\n      }\n    }\n  }\n\n  &.active {\n    @include plain-hover-focus {\n      z-index: 2; // Place active items above their siblings for proper border styling\n      color: $list-group-active-color;\n      text-decoration: none; // Repeat here because it inherits global a:hover otherwise\n      background-color: $list-group-active-bg;\n      border-color: $list-group-active-border;\n\n      // Force color to inherit for custom content\n      .list-group-item-heading,\n      .list-group-item-heading > small,\n      .list-group-item-heading > .small {\n        color: inherit;\n      }\n      .list-group-item-text {\n        color: $list-group-active-text-color;\n      }\n    }\n  }\n}\n\n.list-group-flush {\n  .list-group-item {\n    border-right: 0;\n    border-left: 0;\n    border-radius: 0;\n  }\n}\n\n\n// Interactive list items\n//\n// Use anchor or button elements instead of `li`s or `div`s to create interactive\n// list items. Includes an extra `.active` modifier class for selected items.\n\n.list-group-item-action {\n  width: 100%; // For `<button>`s (anchors become 100% by default though)\n  color: $list-group-link-color;\n  text-align: inherit; // For `<button>`s (anchors inherit)\n\n  .list-group-item-heading {\n    color: $list-group-link-heading-color;\n  }\n\n  // Hover state\n  @include hover-focus {\n    color: $list-group-link-hover-color;\n    text-decoration: none;\n    background-color: $list-group-hover-bg;\n  }\n}\n\n\n// Contextual variants\n//\n// Add modifier classes to change text and background color on individual items.\n// Organizationally, this must come after the `:hover` states.\n\n@include list-group-item-variant(success, $state-success-bg, $state-success-text);\n@include list-group-item-variant(info, $state-info-bg, $state-info-text);\n@include list-group-item-variant(warning, $state-warning-bg, $state-warning-text);\n@include list-group-item-variant(danger, $state-danger-bg, $state-danger-text);\n\n\n// Custom content options\n//\n// Extra classes for creating well-formatted content within `.list-group-item`s.\n\n.list-group-item-heading {\n  margin-top: 0;\n  margin-bottom: $list-group-item-heading-margin-bottom;\n}\n.list-group-item-text {\n  margin-bottom: 0;\n  line-height: 1.3;\n}\n","// List Groups\n\n@mixin list-group-item-variant($state, $background, $color) {\n  .list-group-item-#{$state} {\n    color: $color;\n    background-color: $background;\n  }\n\n  a.list-group-item-#{$state},\n  button.list-group-item-#{$state} {\n    color: $color;\n\n    .list-group-item-heading {\n      color: inherit;\n    }\n\n    @include hover-focus {\n      color: $color;\n      background-color: darken($background, 5%);\n    }\n\n    &.active {\n      @include plain-hover-focus {\n        color: #fff;\n        background-color: $color;\n        border-color: $color;\n      }\n    }\n  }\n}\n","// Credit: Nicolas Gallagher and SUIT CSS.\n\n.embed-responsive {\n  position: relative;\n  display: block;\n  height: 0;\n  padding: 0;\n  overflow: hidden;\n\n  .embed-responsive-item,\n  iframe,\n  embed,\n  object,\n  video {\n    position: absolute;\n    top: 0;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n    height: 100%;\n    border: 0;\n  }\n}\n\n.embed-responsive-21by9 {\n  padding-bottom: percentage(9 / 21);\n}\n\n.embed-responsive-16by9 {\n  padding-bottom: percentage(9 / 16);\n}\n\n.embed-responsive-4by3 {\n  padding-bottom: percentage(3 / 4);\n}\n\n.embed-responsive-1by1 {\n  padding-bottom: percentage(1 / 1);\n}\n",".close {\n  float: right;\n  font-size: ($font-size-base * 1.5);\n  font-weight: $close-font-weight;\n  line-height: 1;\n  color: $close-color;\n  text-shadow: $close-text-shadow;\n  opacity: .2;\n\n  @include hover-focus {\n    color: $close-color;\n    text-decoration: none;\n    cursor: pointer;\n    opacity: .5;\n  }\n}\n\n// Additional properties for button version\n// iOS requires the button element instead of an anchor tag.\n// If you want the anchor version, it requires `href=\"#\"`.\n// See https://developer.mozilla.org/en-US/docs/Web/Events/click#Safari_Mobile\n\n// scss-lint:disable QualifyingElement\nbutton.close {\n  padding: 0;\n  cursor: pointer;\n  background: transparent;\n  border: 0;\n  -webkit-appearance: none;\n}\n// scss-lint:enable QualifyingElement\n","// .modal-open      - body class for killing the scroll\n// .modal           - container to scroll within\n// .modal-dialog    - positioning shell for the actual modal\n// .modal-content   - actual modal w/ bg and corners and stuff\n\n\n// Kill the scroll on the body\n.modal-open {\n  overflow: hidden;\n}\n\n// Container that the modal scrolls within\n.modal {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-modal;\n  display: none;\n  overflow: hidden;\n  // Prevent Chrome on Windows from adding a focus outline. For details, see\n  // https://github.com/twbs/bootstrap/pull/10951.\n  outline: 0;\n  // We deliberately don't use `-webkit-overflow-scrolling: touch;` due to a\n  // gnarly iOS Safari bug: https://bugs.webkit.org/show_bug.cgi?id=158342\n  // See also https://github.com/twbs/bootstrap/issues/17695\n\n  // When fading in the modal, animate it to slide down\n  &.fade .modal-dialog {\n    transition: transform .3s ease-out;\n    transform: translate(0, -25%);\n  }\n  &.in .modal-dialog { transform: translate(0, 0); }\n}\n.modal-open .modal {\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n// Shell div to position the modal with bottom padding\n.modal-dialog {\n  position: relative;\n  width: auto;\n  margin: $modal-dialog-margin;\n}\n\n// Actual modal\n.modal-content {\n  position: relative;\n  background-color: $modal-content-bg;\n  background-clip: padding-box;\n  border: $modal-content-border-width solid $modal-content-border-color;\n  @include border-radius($border-radius-lg);\n  @include box-shadow($modal-content-xs-box-shadow);\n  // Remove focus outline from opened modal\n  outline: 0;\n}\n\n// Modal background\n.modal-backdrop {\n  position: fixed;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  z-index: $zindex-modal-bg;\n  background-color: $modal-backdrop-bg;\n\n  // Fade for backdrop\n  &.fade { opacity: 0; }\n  &.in { opacity: $modal-backdrop-opacity; }\n}\n\n// Modal header\n// Top section of the modal w/ title and dismiss\n.modal-header {\n  padding: $modal-title-padding;\n  border-bottom: $modal-header-border-width solid $modal-header-border-color;\n  @include clearfix;\n}\n// Close icon\n.modal-header .close {\n  margin-top: -2px;\n}\n\n// Title text within header\n.modal-title {\n  margin: 0;\n  line-height: $modal-title-line-height;\n}\n\n// Modal body\n// Where all modal content resides (sibling of .modal-header and .modal-footer)\n.modal-body {\n  position: relative;\n  padding: $modal-inner-padding;\n}\n\n// Footer (for actions)\n.modal-footer {\n  padding: $modal-inner-padding;\n  text-align: right; // right align buttons\n  border-top: $modal-footer-border-width solid $modal-footer-border-color;\n  @include clearfix(); // clear it in case folks use .pull-* classes on buttons\n}\n\n// Measure scrollbar width for padding body during modal show/hide\n.modal-scrollbar-measure {\n  position: absolute;\n  top: -9999px;\n  width: 50px;\n  height: 50px;\n  overflow: scroll;\n}\n\n// Scale up the modal\n@include media-breakpoint-up(sm) {\n  // Automatically set modal's width for larger viewports\n  .modal-dialog {\n    max-width: $modal-md;\n    margin: $modal-dialog-sm-up-margin-y auto;\n  }\n\n  .modal-content {\n    @include box-shadow($modal-content-sm-up-box-shadow);\n  }\n\n  .modal-sm { max-width: $modal-sm; }\n}\n\n@include media-breakpoint-up(lg) {\n  .modal-lg { max-width: $modal-lg; }\n}\n","// Base class\n.tooltip {\n  position: absolute;\n  z-index: $zindex-tooltip;\n  display: block;\n  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.\n  // So reset our font and text properties to avoid inheriting weird values.\n  @include 