  background-color: $tooltip-bg;\n  @include border-radius($border-radius);\n\n  &::before {\n    position: absolute;\n    width: 0;\n    height: 0;\n    border-color: transparent;\n    border-style: solid;\n  }\n}\n","@mixin reset-text {\n  font-family: $font-family-base;\n  // We deliberately do NOT reset font-size or word-wrap.\n  font-style: normal;\n  font-weight: normal;\n  letter-spacing: normal;\n  line-break: auto;\n  line-height: $line-height-base;\n  text-align: left; // Fallback for where `start` is not supported\n  text-align: start;\n  text-decoration: none;\n  text-shadow: none;\n  text-transform: none;\n  white-space: normal;\n  word-break: normal;\n  word-spacing: normal;\n}\n",".popover {\n  position: absolute;\n  top: 0;\n  left: 0;\n  z-index: $zindex-popover;\n  display: block;\n  max-width: $popover-max-width;\n  padding: $popover-inner-padding;\n  // Our parent element can be arbitrary since tooltips are by default inserted as a sibling of their target element.\n  // So reset our font and text properties to avoid inheriting weird values.\n  @include reset-text();\n  font-size: $font-size-sm;\n  // Allow breaking very long words so they don't overflow the popover's bounds\n  word-wrap: break-word;\n  background-color: $popover-bg;\n  background-clip: padding-box;\n  border: $popover-border-width solid $popover-border-color;\n  @include border-radius($border-radius-lg);\n  @include box-shadow($popover-box-shadow);\n\n\n  // Popover directions\n\n  &.popover-top,\n  &.bs-tether-element-attached-bottom {\n    margin-top: -$popover-arrow-width;\n\n    &::before,\n    &::after {\n      left: 50%;\n      border-bottom-width: 0;\n    }\n\n    &::before {\n      bottom: -$popover-arrow-outer-width;\n      margin-left: -$popover-arrow-outer-width;\n      border-top-color: $popover-arrow-outer-color;\n    }\n\n    &::after {\n      bottom: -($popover-arrow-outer-width - 1);\n      margin-left: -$popover-arrow-width;\n      border-top-color: $popover-arrow-color;\n    }\n  }\n\n  &.popover-right,\n  &.bs-tether-element-attached-left {\n    margin-left: $popover-arrow-width;\n\n    &::before,\n    &::after {\n      top: 50%;\n      border-left-width: 0;\n    }\n\n    &::before {\n      left: -$popover-arrow-outer-width;\n      margin-top: -$popover-arrow-outer-width;\n      border-right-color: $popover-arrow-outer-color;\n    }\n\n    &::after {\n      left: -($popover-arrow-outer-width - 1);\n      margin-top: -($popover-arrow-outer-width - 1);\n      border-right-color: $popover-arrow-color;\n    }\n  }\n\n  &.popover-bottom,\n  &.bs-tether-element-attached-top {\n    margin-top: $popover-arrow-width;\n\n    &::before,\n    &::after {\n      left: 50%;\n      border-top-width: 0;\n    }\n\n    &::before {\n      top: -$popover-arrow-outer-width;\n      margin-left: -$popover-arrow-outer-width;\n      border-bottom-color: $popover-arrow-outer-color;\n    }\n\n    &::after {\n      top: -($popover-arrow-outer-width - 1);\n      margin-left: -$popover-arrow-width;\n      border-bottom-color: $popover-title-bg;\n    }\n\n    // This will remove the popover-title's border just below the arrow\n    .popover-title::before {\n      position: absolute;\n      top: 0;\n      left: 50%;\n      display: block;\n      width: 20px;\n      margin-left: -10px;\n      content: \"\";\n      border-bottom: 1px solid $popover-title-bg;\n    }\n  }\n\n  &.popover-left,\n  &.bs-tether-element-attached-right {\n    margin-left: -$popover-arrow-width;\n\n    &::before,\n    &::after {\n      top: 50%;\n      border-right-width: 0;\n    }\n\n    &::before {\n      right: -$popover-arrow-outer-width;\n      margin-top: -$popover-arrow-outer-width;\n      border-left-color: $popover-arrow-outer-color;\n    }\n\n    &::after {\n      right: -($popover-arrow-outer-width - 1);\n      margin-top: -($popover-arrow-outer-width - 1);\n      border-left-color: $popover-arrow-color;\n    }\n  }\n}\n\n\n// Offset the popover to account for the popover arrow\n.popover-title {\n  padding: $popover-title-padding-y $popover-title-padding-x;\n  margin: 0; // reset heading margin\n  font-size: $font-size-base;\n  background-color: $popover-title-bg;\n  border-bottom: $popover-border-width solid darken($popover-title-bg, 5%);\n  $offset-border-width: ($border-width / $font-size-root);\n  @include border-radius(($border-radius-lg - $offset-border-width) ($border-radius-lg - $offset-border-width) 0 0);\n\n  &:empty {\n    display: none;\n  }\n}\n\n.popover-content {\n  padding: $popover-content-padding-y $popover-content-padding-x;\n}\n\n\n// Arrows\n//\n// .popover-arrow is outer, .popover-arrow::after is inner\n\n.popover::before,\n.popover::after {\n  position: absolute;\n  display: block;\n  width: 0;\n  height: 0;\n  border-color: transparent;\n  border-style: solid;\n}\n\n.popover::before {\n  content: \"\";\n  border-width: $popover-arrow-outer-width;\n}\n.popover::after {\n  content: \"\";\n  border-width: $popover-arrow-width;\n}\n","// Wrapper for the slide container and indicators\n.carousel {\n  position: relative;\n}\n\n.carousel-inner {\n  position: relative;\n  width: 100%;\n  overflow: hidden;\n\n  > .carousel-item {\n    position: relative;\n    display: none;\n    transition: .6s ease-in-out left;\n\n    // Account for jankitude on images\n    > img,\n    > a > img {\n      @extend .img-fluid;\n      line-height: 1;\n    }\n\n    // WebKit CSS3 transforms for supported devices\n    @media all and (transform-3d), (-webkit-transform-3d) {\n      transition: transform .6s ease-in-out;\n      backface-visibility: hidden;\n      perspective: 1000px;\n\n      &.next,\n      &.active.right {\n        left: 0;\n        transform: translate3d(100%, 0, 0);\n      }\n      &.prev,\n      &.active.left {\n        left: 0;\n        transform: translate3d(-100%, 0, 0);\n      }\n      &.next.left,\n      &.prev.right,\n      &.active {\n        left: 0;\n        transform: translate3d(0, 0, 0);\n      }\n    }\n  }\n\n  > .active,\n  > .next,\n  > .prev {\n    display: block;\n  }\n\n  > .active {\n    left: 0;\n  }\n\n  > .next,\n  > .prev {\n    position: absolute;\n    top: 0;\n    width: 100%;\n  }\n\n  > .next {\n    left: 100%;\n  }\n  > .prev {\n    left: -100%;\n  }\n  > .next.left,\n  > .prev.right {\n    left: 0;\n  }\n\n  > .active.left {\n    left: -100%;\n  }\n  > .active.right {\n    left: 100%;\n  }\n}\n\n\n//\n// Left/right controls for nav\n//\n\n.carousel-control {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  width: $carousel-control-width;\n  font-size: $carousel-control-font-size;\n  color: $carousel-control-color;\n  text-align: center;\n  text-shadow: $carousel-text-shadow;\n  opacity: $carousel-control-opacity;\n  // We can't have this transition here because WebKit cancels the carousel\n  // animation if you trip this while in the middle of another animation.\n\n  // Set gradients for backgrounds\n  &.left {\n    @include gradient-x($start-color: rgba(0,0,0,.5), $end-color: rgba(0,0,0,.0001));\n  }\n  &.right {\n    right: 0;\n    left: auto;\n    @include gradient-x($start-color: rgba(0,0,0,.0001), $end-color: rgba(0,0,0,.5));\n  }\n\n  // Hover/focus state\n  @include hover-focus {\n    color: $carousel-control-color;\n    text-decoration: none;\n    outline: 0;\n    opacity: .9;\n  }\n\n  // Toggles\n  .icon-prev,\n  .icon-next {\n    position: absolute;\n    top: 50%;\n    z-index: 5;\n    display: inline-block;\n    width: $carousel-icon-width;\n    height: $carousel-icon-width;\n    margin-top: -($carousel-icon-width / 2);\n    font-family: serif;\n    line-height: 1;\n  }\n  .icon-prev {\n    left: 50%;\n    margin-left: -($carousel-icon-width / 2);\n  }\n  .icon-next {\n    right: 50%;\n    margin-right: -($carousel-icon-width / 2);\n  }\n\n  .icon-prev {\n    &::before {\n      content: \"\\2039\";// SINGLE LEFT-POINTING ANGLE QUOTATION MARK (U+2039)\n    }\n  }\n  .icon-next {\n    &::before {\n      content: \"\\203a\";// SINGLE RIGHT-POINTING ANGLE QUOTATION MARK (U+203A)\n    }\n  }\n}\n\n\n// Optional indicator pips\n//\n// Add an unordered list with the following class and add a list item for each\n// slide your carousel holds.\n\n.carousel-indicators {\n  position: absolute;\n  bottom: 10px;\n  left: 50%;\n  z-index: 15;\n  width: $carousel-indicators-width;\n  padding-left: 0;\n  margin-left: -($carousel-indicators-width / 2);\n  text-align: center;\n  list-style: none;\n\n  li {\n    display: inline-block;\n    width: $carousel-indicator-size;\n    height: $carousel-indicator-size;\n    margin: 1px;\n    text-indent: -999px;\n    cursor: pointer;\n    // IE9 hack for event handling\n    //\n    // Internet Explorer 9 does not properly handle clicks on elements with a `background-color` of `transparent`,\n    // so we use `rgba(0,0,0,0)` instead since it's a non-buggy equivalent.\n    // See https://developer.mozilla.org/en-US/docs/Web/Events/click#Internet_Explorer\n    background-color: rgba(0,0,0,0); // IE9\n    border: 1px solid $carousel-indicator-border-color;\n    border-radius: $carousel-indicator-size;\n  }\n\n  .active {\n    width: $carousel-indicator-active-size;\n    height: $carousel-indicator-active-size;\n    margin: 0;\n    background-color: $carousel-indicator-active-bg;\n  }\n}\n\n\n// Optional captions\n//\n// Hidden by default for smaller viewports.\n\n.carousel-caption {\n  position: absolute;\n  right: ((100% - $carousel-caption-width) / 2);\n  bottom: 20px;\n  left: ((100% - $carousel-caption-width) / 2);\n  z-index: 10;\n  padding-top: 20px;\n  padding-bottom: 20px;\n  color: $carousel-caption-color;\n  text-align: center;\n  text-shadow: $carousel-text-shadow;\n\n  .btn {\n    text-shadow: none; // No shadow for button elements in carousel-caption\n  }\n}\n\n\n//\n// Responsive variations\n//\n\n@include media-breakpoint-up(sm) {\n  // Scale up the controls a smidge\n  .carousel-control {\n    .icon-prev,\n    .icon-next {\n      width: $carousel-control-sm-up-size;\n      height: $carousel-control-sm-up-size;\n      margin-top: -($carousel-control-sm-up-size / 2);\n      font-size: $carousel-control-sm-up-size;\n    }\n    .icon-prev {\n      margin-left: -($carousel-control-sm-up-size / 2);\n    }\n    .icon-next {\n      margin-right: -($carousel-control-sm-up-size / 2);\n    }\n  }\n\n  // Show and left align the captions\n  .carousel-caption {\n    right: ((100% - $carousel-caption-sm-up-width) / 2);\n    left: ((100% - $carousel-caption-sm-up-width) / 2);\n    padding-bottom: 30px;\n  }\n\n  // Move up the indicators\n  .carousel-indicators {\n    bottom: 20px;\n  }\n}\n",".align-baseline { vertical-align: baseline !important; } // Browser default\n.align-top { vertical-align: top !important; }\n.align-middle { vertical-align: middle !important; }\n.align-bottom { vertical-align: bottom !important; }\n.align-text-bottom { vertical-align: text-bottom !important; }\n.align-text-top { vertical-align: text-top !important; }\n","//\n// Contextual backgrounds\n//\n\n.bg-faded {\n  background-color: $gray-lightest;\n}\n\n@include bg-variant('.bg-primary', $brand-primary);\n\n@include bg-variant('.bg-success', $brand-success);\n\n@include bg-variant('.bg-info', $brand-info);\n\n@include bg-variant('.bg-warning', $brand-warning);\n\n@include bg-variant('.bg-danger', $brand-danger);\n\n@include bg-variant('.bg-inverse', $brand-inverse);\n","// Contextual backgrounds\n\n@mixin bg-variant($parent, $color) {\n  #{$parent} {\n    background-color: $color !important;\n  }\n  a#{$parent} {\n    @include hover-focus {\n      background-color: darken($color, 10%) !important;\n    }\n  }\n}\n","//\n// Border-width\n//\n\n// TBD...?\n\n\n//\n// Border-radius\n//\n\n.rounded {\n  @include border-radius($border-radius);\n}\n.rounded-top {\n  @include border-top-radius($border-radius);\n}\n.rounded-right {\n  @include border-right-radius($border-radius);\n}\n.rounded-bottom {\n  @include border-bottom-radius($border-radius);\n}\n.ro